@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap');

.wrapper{
  max-width: 1080px;
  margin: 50px auto;
  padding: 0 20px;
  position: relative;
}

.wrapper .center-line{
  position: absolute;
  height: calc(100% + 80px);
  width: 4px;
  
  left: 50%;
  top: 20px;
  transform: translateX(-50%);
  box-shadow: 0 0 10px 2px #e0e0e5;
}

.wrapper .row{
  display: flex;;
}

.wrapper .row-1{
  justify-content: flex-start;
}
.wrapper .row-2{
  justify-content: flex-end;
}

.wrapper .row section{
  /* background: var(--palette-blackgray); */
  color:dimgrey;
  color: white;
  border-radius: 5px;
  width: calc(50% - 40px);
  padding: 40px;
  position: relative;
  border:#0c2340 2px;
  box-shadow: 2px 2px 10px 2px #76bbce; 
}

/* .wrapper .row section::before{
  position: absolute;
  content: "";
  height: 15px;
  width: 15px;
  background: #fff;
  top: 28px;
  z-index: -1;
  transform: rotate(45deg);
} */

.row-1 section::before{
  right: -7px;
}

.row-2 section::before{
  left: -7px;
}

section > .icon > img{
  height: 15px;
}

.row section .icon,
.center-line .scroll-icon{
    display: flex;
    align-items: center;
    justify-content: center;
   position: absolute;
    /* background: var(--palette-blackgray); */
    background-color: #000;
    height: 40px;
    width: 40px;
    text-align: center;
    line-height: 40px;
    border-radius: 50%;
    color:#6fc3e1;
    font-size: 17px;
    border: 1px #0c2340 solid;
    /* box-shadow: 0 0 0 4px var(--palette-yellow), inset 0 2px 0 rgba(0,0,0,0.08), 0 3px 0 4px rgba(0,0,0,0.05); */
    box-shadow: 0 0 10px 3px #e1dcdb;
}

.center-line .scroll-icon{
  bottom: 0px;
  left: 50%;
  font-size: 25px;
  transform: translateX(-50%) !important;
  transition: .2s ease;
}

.center-line .scroll-icon:hover{
  background-color:#0c2340;
  transition: .2s ease;
}

.center-line .scroll-icon:hover > .MuiSvgIcon-root{
  color: var(--palette-blackgray);
  transition: .2s ease;
}

.row-1 section .icon{
  top: 15px;
  right: -60px;
}

.row-2 section .icon{
  top: 15px;
  left: -60px;
}
.row section .details,
.row section .bottom{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.row section .details .title{
  font-size: 22px;
  font-weight: 600;
}
.details>span{
  font-family: 'Fjalla One', sans-serif;
  letter-spacing: 2px;
}

.row section p{
  margin: 10px 0 17px 0;
}
.row section .bottom a{
  text-decoration: none;
  background: #947d7d;
  color: #fff;
  padding: 7px 15px;
  border-radius: 5px;
  /* font-size: 17px; */
  font-weight: 400;
  transition: all 0.3s ease;
}

.row section .bottom a:hover{
  transform: scale(0.97);
}

@media(max-width: 790px){
  .wrapper .center-line{
    left: 40px;
  }
  .wrapper .row{
    margin: 30px 0 3px 60px;
  }
  .wrapper .row section{
    width: 100%;
  }
  .row-1 section::before{
    left: -7px;
  }
  .row-1 section .icon{
    left: -60px;
  }
}

@media(max-width: 440px){
  .wrapper .center-line,
  .row section::before,
  .row section .icon{
    display: none;
  }
  .wrapper .row{
    margin: 10px 0;
  }
}